.App {
  background-color: #00001a;
  font-size: calc(10px + 2vmin);
  color: white;
  display: block;
  padding: 0;
  margin: 0;
  min-width: 100vw;
  min-height: 100vh;
}

#page {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.hr {
  border: 5px solid white;
  margin-right: 2rem;
  margin-left: 2rem;
}

@media (min-width: 600px) {
  .App {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  .hr {
    margin-right: 4rem;
    margin-left: 4rem;
  }
  

}
